.formation-table-div {
    position: absolute;
    right: 0;
    width: 33%;
    height: 83vh;
    border: .25vh solid #353b48;
    border-radius: 5%;
    margin: 3vh;
}

.formation-table-header {
    color: white;
    text-align: center;
    padding: 1vh;
    font-size: 2vh;
}

.formation-table-header h4 {
    padding: 1vh;
    border-bottom: .25vh solid #0984e3;
    border-radius: 10%;
}

.formation-table-footer {
    text-align: center;
    margin: 3%;
    height: 6%;
    min-height: 25px;
}

.formation-table-div .table {
    margin-left: 2vh;
    margin-right: 2vh;
    font-size: 2vh;
    background-color: white;
    color: #252830;
    border: .25vh solid #353b48;
    font-family: 'Audiowide';
    height: 75%;
    border-radius: 2.5%;
    
}