.draw-tool {
    width: 100%;
    background-color: grey;
    height: auto;
}

.draw-div {
    display: none;
    height: 90%;
    border: .25vh solid #353b48;

    margin: 1vh;
}

.view-draw-div {
    overflow-y: auto;
    display: none;
    justify-content: center;
    width: 100%;
    height: 80%;
    background-color: white;
}

.draw-formation {
    overflow-y: auto;
    display: inline-flex;
    justify-content: center;
    width: 100%;
    height: 80%;
}

.drawing-area {
    position: relative;
    width: 700px;
    height: 700px;
    background-color: white;
    margin: 1vh;
}

.drawing-toolbar {
    display: inline-flex;
    justify-content: space-evenly;
    position: relative;
    z-index: 1;;
    width: 100%;
    height: 10vh;
}

.toolbar-action-div {

    display: inline-block;
    text-align: center;
    width: 25%;
    height: 100%;
    font-size: 1.5vh;
    padding: 1vh;
}

.toolbar-action-div h2 {
    width: 100%;
    color: white;
}

.toolbar-actions {
    width: 100%;
    display: inline-flex;
    justify-content: space-evenly;
}

.toolbar-actions button {
    width: 50%;
    font-size: 4vh;
    font-weight: bold;
    height: 100%;
    background-color: rgba(0, 0, 0, 0);
    border: .1vh solid white;
    margin: 1vh;
    border-radius: 1vh;
    color: #0984e3;
}

.toolbar-actions button:hover {
    transform: scale(1.1);
    transition: .25s ease-in-out;
    background-color: white;
}

.toggle-draw {
    display: none;
    justify-content: center;
    width: 100%;
}

.toggle-draw button {
    cursor: pointer;
    color: #0984e3;
    font-family: "Audiowide";
    border: .1vh solid white;
    border-radius: 1vh;
    padding: 1vh;
    font-size: 18px;
    background-color: rgba(0, 0, 0, 0);
    width: auto;
    height: auto;
}

.toggle-draw button:hover {
    transform: scale(1.1);
    transition: .25s ease-in-out;
    background-color: white;
}

.toggle-draw .flex-btn-div {
    width: 100%;
    max-height: 75px;
    padding: 1vh;
    display: inline-flex;
    overflow-x: auto;
}

.toggle-draw .btn-div {
    width: auto;

    display: inline-flex;
    justify-content: center;
}