@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.formation-page {
    width: 100%;
    height: 100%;
}

.formation-body-div {
    width: 100%;
    height: 100%;
}

.formation-body {
    display: inline-block;
    width: 64%;
    height: 83vh;
}

.edit-formation {
    display: none;
    width: 100%;
    height: auto;
    background-color: #0a0d14;
    border-bottom: .25vh solid #353b48;
    border-right: .25vh solid #353b48;
    padding: .5vh;
}

.edit-options {
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

.edit-formation .user-message {
    width: 100%;
    text-align: center;
    color: #d04436;
}

.input-box {
    color: white;
    font-family: 'Audiowide';
    background-color: #0984e3;
}

.logo-div {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.cancel-toolbar {
    display: inline-flex;
    justify-content: center;
    width: 100%;
    height: 5vh;
}

.load-logo {
    width: 50%;
}

.alert-div {
    z-index: 10000 !important;
    display: fixed;
    bottom: 0;
    margin: auto;
    width: 100%;
}

.flex-btn-div {
    display: inline-block;
    width: 100%;
    vertical-align: middle;
    justify-content: space-evenly;

}

@media screen and  (max-width: 800px){
    body {
        overflow-y: auto;
    }

    .formation-table-div  {
        position: relative !important;
        display: inline-block !important;
        width: 90% !important;
        margin-left: 5% !important;
        padding: 0;
    } 

    .formation-body {
        display: inline-block;
        width: 100%;

    } 

    .edit-formation {
        display: inline-block;
        width: 100%;
        margin: 0;
        padding: 0;
    }

    .edit-options {
        display: inline-block;
        text-align: center;
        width: 90%;
        padding-left: 5% !important;
        padding-bottom: 1vh;
    }

    .flex-btn-div {
        width: 90% !important;
        padding-left: 5% !important;
    }

    .drawing-area {
        opacity: 1;
        background-color: red;
        overflow: scroll
    }
    
    .draw-div {
        margin-right: 25%;
        width: 100%;
    }
}